<template>
  <div class="toast-container position-absolute pe-3 top-0 end-0">
    <Toast v-for="(msg, key) in messages" :key="key" :msg="msg" />
  </div>
</template>

<script>
import Toast from "./Toast.vue";
export default {
  data() {
    return {
      messages: [],
    };
  },
  components: {
    Toast,
  },
  created() {
    this.$root.emitter.on("push-message", this.addMessage);
  },
  methods: {
    addMessage(message) {
      this.messages.push(message);
      setTimeout(() => this.messages.shift(), 3000); // Automatically remove the message after 3 seconds
    },
  },
};
</script>

